* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  background-image: url('./assets/search-360.jpg');
  background-size: cover;
  background-position: bottom;
  background-repeat: repeat;
  transition: 0.4 ease-in-out;
  color: white;

  &.snow {
  background-image: url('./assets/snow-360.jpg');
  }

  &.sun {
  background-image: url('./assets/sun-360.jpg');
  }

  &.rain {
  background-image: url('./assets/rain-360.jpg');
  }

  &.clouds {
  background-image: url('./assets/clouds-360.jpg');
  }

  &.clear {
  background-image: url('./assets/clear-360.jpg');
  }

  &__search-box {
    width: 100%;
    margin: 0 0 80px;
  }

  &__search-bar {
    display: block;
    width: 100%;
    padding: 7px;

    appearance: none;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 10px 10px 25px 25px;

    box-shadow: 0px 5px rgba(0, 0, 0, 0.1);

    color: #313131;
    font-size: 20px;

    transition: 0.4s ease-in-out;

    &:focus {
      background-color: rgba(255, 255, 255, 0.6);
      padding: 15px;
    }
  }

  &__location-box {
    & .location {
      color: #fff;
      font-weight: 500;
      font-size: 32px;
      text-align: center;
      text-shadow: 3px 3px rgba(50, 50, 70, 0.5);

      margin-bottom: 15px;
    }
    & .date {
      color: #fff;
      font-weight: 300;
      font-size: 22px;
      text-align: center;
      font-style: italic;
      text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
    }
  }

  &__weather-box {
    text-align: center;
    & .temp {
      position: relative;
      display: inline-block;
      margin: 30px auto;
      background-color: rgba(255, 255, 255, 0.2);
      border-radius: 30%;
      text-align: center;

      padding: 15px 25px;

      color: #fff;
      font-size: 88px;
      font-weight: 900;

      text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
      box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    }
    & .weather {
      color: #fff;
      font-size: 48px;
      font-weight: 700;
      text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    }
  }
  &__find-page {
    color: transparent;
    text-shadow: 0 -30px 2px rgba(255,255,255,0.9);
    text-align: center;
    font-weight: 600;
    font-size: 36px;
  }
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8));
  padding: 25px;
}